@charset "UTF-8";@import url(http://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900|Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,800,300|Open+Sans+Condensed:300,700,300italic|Merriweather:400,900italic,700italic,400italic,300italic,900,700,300);@font-face{font-family:"Roboto-Condensed-Regular";src:url("../assets/css/fonts/RobotoCondensed-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Regular";src:url("../assets/css/fonts/Roboto-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Light-Italic";src:url("../assets/css/fonts/Roboto-LightItalic.ttf") format("truetype")}.anim-fast{-webkit-transition:all .3s ease;transition:all .3s ease}.all-transitions{-webkit-transition:all .5s ease-out;transition:all .5s ease-out}.main_shadow{box-shadow:0 0 5px 2px rgba(0,0,0,0.22)}#myeditor{height:100%;padding:0 10px}#editor_container{width:100%;height:100%;z-index:2;-webkit-transition:-webkit-transform 300ms ease-in-out;transition:-webkit-transform 300ms ease-in-out;transition:transform 300ms ease-in-out;transition:transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out}#editor_container.slide{-webkit-transform:translateY(420px);transform:translateY(420px)}#editor_container #editor{height:100%}#editor_container ul.nav_tabs{background-color:white;border-bottom:0;border-radius:1px;padding-left:0;margin-bottom:0;list-style:none;color:black}#editor_container ul.nav_tabs li{display:inline-block;padding:15px 20px;cursor:pointer;-webkit-transition:all .3s ease;transition:all .3s ease}#editor_container ul.nav_tabs li.active{border-bottom:4px solid #f5d328}#editor_container ul.nav_tabs li:hover{background-color:rgba(0,0,0,0.1)}#editor_container ul.nav_tabs li a{margin-left:15px;font-size:.8em;margin-bottom:0;border-radius:2px 2px 0 0;border:0}#project-options{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;padding:25px 0 25px 0}.ace_gutter{background:transparent !im