@charset "UTF-8";@import url(http://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900|Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,800,300|Open+Sans+Condensed:300,700,300italic|Merriweather:400,900italic,700italic,400italic,300italic,900,700,300);@font-face{font-family:"Roboto-Condensed-Regular";src:url("../assets/css/fonts/RobotoCondensed-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Regular";src:url("../assets/css/fonts/Roboto-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Light-Italic";src:url("../assets/css/fonts/Roboto-LightItalic.ttf") format("truetype")}.anim-fast{-webkit-transition:all .3s ease;transition:all .3s ease}.all-transitions{-webkit-transition:all .5s ease-out;transition:all .5s ease-out}.main_shadow{box-shadow:0 0 5px 2px rgba(0,0,0,0.22)}#device_info{cursor:pointer;text-align:center;bottom:0;position:absolute;font-size:.8em;font-weight:100;font-family:'Open Sans';padding:15px 52px}#device_info:hover{background-color:rgba(255,255,255,0.2)}#device_info p{margin:0 0 0}#device_info i{padding:4px 20px}#device_info i.rotated{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}#device_frame{-webkit-transition:all .5s ease-out;transition:all .5s ease-out;height:64px;width:36px;background:transparent none repeat scroll 0 0;border:1px solid rgba(255,255,255,0.53);margin:12px 6px;padding:3px;border-radius:3px}#device_frame.rotate{-webkit-transform:rotate3d(0, 0, 1, 90deg);transform:rotate3d(0, 0, 1, 90deg)}#device_frame.rotate *{-webkit-transform:translate3d(0, 3px, 0);transform:translate3d(0, 3px, 0)}#device_frame #device_screen{-webkit-transition:all .5s ease-out;transition:all .5s ease-out;width:35px;height:55px;background:rgba(255,255,255,0.06) none repeat scroll 0 0;border:1px solid rgba(255,255,255,0.17)}


