@charset "UTF-8";@import url(http://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900|Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,800,300|Open+Sans+Condensed:300,700,300italic|Merriweather:400,900italic,700italic,400italic,300italic,900,700,300);@font-face{font-family:"Roboto-Condensed-Regular";src:url("../assets/css/fonts/RobotoCondensed-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Regular";src:url("../assets/css/fonts/Roboto-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Light-Italic";src:url("../assets/css/fonts/Roboto-LightItalic.ttf") format("truetype")}.anim-fast{-webkit-transition:all .3s ease;transition:all .3s ease}.all-transitions{-webkit-transition:all .5s ease-out;transition:all .5s ease-out}.main_shadow{box-shadow:0 0 5px 2px rgba(0,0,0,0.22)}.popover{background-color:#fff;color:#575757;font-family:'Open Sans';font-size:1em;position:absolute;width:300px;border-radius:3px;z-index:5;border:0 solid black;padding:20px;opacity:1;z-index:1}.popover ul .key{font-weight:800}.popover ul li{padding-bottom:.5em}.popover:before{content:' ';width:25px;height:25px;position:absolute;bottom:auto;border-right-color:#000;background-color:white;-webkit-transform:rotate(45deg);transform:rotate(45deg);border-left:0 solid black;border-bottom:0 solid black;border-radius:2px;z-index:0}.popover.left:before{left:-10px;bottom:40px;box-shadow:-5px 3px 5px 0 rgba(0,0,0,0.22)}.popover.right{min-height:200px}.popover.right:before{right:-10px;top:15px;box-shadow:5px -3px 5px 0 rgba(0,0,0,0.22)}


